import React from 'react';
import { Dispatch } from "redux"
import { Action, getUsers, IUser, UsersState } from '../../store/users/actionsCreator';
import { connect, ConnectedProps } from 'react-redux';
import { bindActionCreators , compose} from 'redux';
import Table, { ColumnsType } from 'antd/lib/table/Table';
import { ColumnType } from 'antd/lib/table';
import { Button, PageHeader, Space } from 'antd';
import { RootState } from '../../store/reducer';

import {withRouter, RouteComponentProps} from 'react-router-dom';


const mapStateToProps = (state: RootState) => ( {
  users : state.usersReducer.users
})


  const mapDispatchToProps = (dispatch: any) =>
    bindActionCreators({ getUsers }, dispatch);

  const connector = connect(mapStateToProps, mapDispatchToProps);




  type HeaderProps = ConnectedProps<typeof connector>;

class UserList extends React.Component<HeaderProps & RouteComponentProps> {

  static get Columns() : ColumnType<IUser>[] { 
    return [
      {
        title: 'Name',
        dataIndex: 'firstName',
        key: 'firstName',
      },
      {
        title: '',
        dataIndex: 'lastName',
        key: 'lastName',
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
        ellipsis: true,
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, record) => (
          <Space size="middle">
            <a>Edit</a>
          </Space>
        ),
      },
    ];
  } 
    componentDidMount() {
      console.log('mount');
        this.props.getUsers();
        
    }

    renderView(data : IUser[] ) {
      console.log(data);
      if (data && data.length > 0)
      {
        console.log('table');
        const col = UserList.Columns;
        return(<Table dataSource={data} columns={col} />);
      }
      else {
        console.log('empty');
        return(<div>Empty</div>);
      }
    }
    
    render() {
      const  { users }   = this.props;


      return(
        <div>
          <PageHeader
            style={{ paddingLeft: 0, paddingRight: 0}}
            className="site-page-header"
            title="List of tasks"
             subTitle=""
            extra={[
              <Button key='buttonAdd' type="primary">Add New</Button> 
            ]}>  
        </PageHeader>
          {this.renderView(users)}
        </div>
      )
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(UserList));