import { Button, Col, DatePicker, Form, FormInstance, Input, PageHeader, Row, Select, TimePicker } from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import axios from 'axios';
import React from 'react';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { ITask } from '../../store/tasks/actionsCreator';

class NewTask extends React.Component<RouteComponentProps> {
    formRef = React.createRef<FormInstance>();
    
    onFinish = (values: any) => {
        console.log(values);
        const task : ITask = {
            date : values.date.toDate(),
            dueDate: values.dueDate.toDate(),
            durationInSeconds : 10,
            key : (values.duration.hours() * 60) + values.duration.minutes(),
            locationId : values.locationId,
            name : values.name,
            notes : values.notes,
            typeId: values.typeId
        }
        console.log(task);

        axios.post('http://tskrai-api.azurewebsites.net/tasks', task)
          .then(function (response) {
            console.log('ok');
            console.log(response);
          })
          .catch(function (error) {
            console.log('coco');
            console.log(error);
          });
      };

    onCancel = () => {
        this.props.history.goBack();//push('/tasklist/new')
    }
      /*
      moment().format('YYYY-MM-DD[T]HH:mm:ss.SSSZ')
       */

    

    render() {
        const options = [{ label: 'Cleaning', value: 1}, 
            { label: 'CheckIn', value: 2 }, 
            { label: 'Delivery', value: 3 }]

            const locations = [{ label: 'Rua 5 de Outubro', value: 1}, 
            { label: 'Rua do Bonfim', value: 2 }, 
            { label: 'Nao sei onde moras', value: 3 }]
            const timeFormat = 'HH:mm';
            const dateFormat = 'yy-MM-DD HH:mm';

            const layout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
              };

            const freeText = { span: 3 };

        return (<div>
        <Form {...layout} ref={this.formRef} name="control-ref" onFinish={this.onFinish}>
            <PageHeader
            onBack={this.onCancel}
              title="New Task ?"
               subTitle="Fill all the information to create a new task"
              extra={[
                <Button key='buttonSave' type="primary" htmlType="submit">Save</Button> ,
                <Button key='buttonCancel' type='ghost' onClick={this.onCancel}  >Cancel</Button> 
              ]}>  
          </PageHeader>
          <div>
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                <Col span={12}>
                  <Form.Item
                    label="Name"
                    name="name">
                    <Input />
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item
                    label="Task type"
                    name="typeId">
                    <Select
                        placeholder="Select a type"
                        options={options}
                        allowClear>
                    </Select>
                  </Form.Item>
                </Col>
            </Row>
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                <Col span={12}>
                  <Form.Item
                    label="Date"
                    name="date">
                    <DatePicker  showTime style={{ width: '100%' }}/>
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item
                    label="Location"
                    name="locationId">
                    <Select
                        placeholder="Select a type"
                        options={locations}
                        allowClear>
                    </Select>
                  </Form.Item>
                </Col>
            </Row>
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                <Col span={12}>
                  <Form.Item
                    label="Due Date"
                    name="dueDate">
                    <DatePicker format={dateFormat} showTime style={{ width: '100%' }}/>
                  </Form.Item>
                </Col>
                <Col className="gutter-row" span={12}>
                  <Form.Item
                    label="Duration"
                    name="duration">
                     <TimePicker minuteStep={5} format={timeFormat} style={{ width: '100%' }} />
                  </Form.Item>
                </Col>
            </Row>
            <Row>
                <Col className="gutter-row" span={24}>
            <Form.Item
                    labelCol={freeText}
                    wrapperCol={{ span: 21}}
                    label="Notes"
                    name="notes">
                     <TextArea
                    style={{ width: '100%' }}
                placeholder="Controlled autosize"
                autoSize={{ minRows: 3, maxRows: 5 }}
                /> 
              </Form.Item>
              </Col>
            </Row>
          </div>
          </Form>
          </div>);
    }
}

export default withRouter(NewTask);