import React from 'react';
import { Dispatch } from "redux"
import { Action, getTasks, ITask, TasksState } from '../../store/tasks/actionsCreator';
import { connect, ConnectedProps } from 'react-redux';
import { bindActionCreators , compose} from 'redux';
import Table, { ColumnsType } from 'antd/lib/table/Table';
import { ColumnType } from 'antd/lib/table';
import { Button, PageHeader, Space } from 'antd';
import { RootState } from '../../store/reducer';

import {withRouter, RouteComponentProps} from 'react-router-dom';


const mapStateToProps = (state: RootState) => ( {
  tasks : state.tasksReducer.tasks
})


  const mapDispatchToProps = (dispatch: any) =>
    bindActionCreators({ getTasks }, dispatch);

  const connector = connect(mapStateToProps, mapDispatchToProps);




  type HeaderProps = ConnectedProps<typeof connector>;

class TaskList extends React.Component<HeaderProps & RouteComponentProps> {

  static get Columns() : ColumnType<ITask>[] { 
    return [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        ellipsis: true,
      },
      {
        title: 'Date',
        dataIndex: 'date',
        key: 'date',
        ellipsis: true,
      },
      {
        title: 'Due Date',
        dataIndex: 'dueDate',
        key: 'dueDate',
        ellipsis: true,
      },
      {
        title: 'Duration',
        dataIndex: 'durationInSeconds',
        key: 'durationInSeconds',
      },
      {
        title: 'Notes',
        dataIndex: 'notes',
        key: 'notes',
        ellipsis: true,
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, record) => (
          <Space size="middle">
            <a>Edit</a>
          </Space>
        ),
      },
    ];
  } 
    componentDidMount() {
      console.log('mount');
        this.props.getTasks();
        
    }

    renderView(data : ITask[] ) {
      console.log(data);
      if (data && data.length > 0)
      {
        console.log('table');
        const col = TaskList.Columns;
        return(<Table dataSource={data} columns={col} />);
      }
      else {
        console.log('empty');
        return(<div>Empty</div>);
      }
    }
    
    render() {
      const  { tasks }   = this.props;


      return(
        <div>
          <PageHeader
            style={{ paddingLeft: 0, paddingRight: 0}}
            className="site-page-header"
            title="List of tasks"
             subTitle=""
            extra={[
              <Button key='buttonAdd' type="primary" onClick={() => this.props.history.push('/tasklist/new')}>Add New</Button> 
            ]}>  
        </PageHeader>
          {this.renderView(tasks)}
        </div>
      )
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TaskList));