import { ActionType } from "./actionTypes"
import { Dispatch } from 'redux';
import axios from 'axios';

export interface ITask {
    key: number,
    name: string
    typeId: number
    locationId: string
    date: Date
    dueDate: Date
    durationInSeconds: number
    notes: string | undefined
  }
  
  export type TasksState = {
    tasks : ITask[]
  }
  
  export type AddTaskAction = {
    type: ActionType.ADD_TASK
    payload: ITask
  }

  export type GetTasksAction = {
    type: ActionType.GET_TASKS
    payload: ITask[]
  }
  
export type Action =
    | AddTaskAction
    | GetTasksAction;
  


export const getTasks = () => {
  return async (dispatch: Dispatch<Action>) => {
    try {
        const { data } = await axios.get('http://tskrai-api.azurewebsites.net/tasks');

        
         dispatch({
          type: ActionType.GET_TASKS,
          payload: data.map((task :ITask, index: number) => { 
            task.key = index;
            return task;
          }),
        });
      } catch (err) {
        console.log(err);
      }    
  }
}
