import { ActionType } from "./actionTypes"
import { Dispatch } from 'redux';
import axios from 'axios';

export interface IUser {
    key: number,
    id: number,
    firstName: string
    lastName: string
    email: string
  }
  
  export type UsersState = {
    users : IUser[]
  }


  export type GetUsersAction = {
    type: ActionType.GET_USERS
    payload: IUser[]
  }
  
export type Action =
    | GetUsersAction;

export const getUsers = () => {
  return async (dispatch: Dispatch<Action>) => {
    try {
      const { data } = await axios.get('http://tskrai-api.azurewebsites.net/user', {
          params: {
            availabilityStartDate: null,
            availabilityEndDate: null,
            latitude: null,
            longitude : null,
            name :null
         }
      });
      
      dispatch({
        type: ActionType.GET_USERS,
        payload: data.map((user :IUser, index: number) => { 
          user.key = index;
          return user;
        }),
      });
    } catch (err) {
      console.log(err);
    }    
  }
}